interface LicenseRequestMeta {
    url: string;
    request: string;
    response: string;
}

export function getLicenseRequestsFromHar(file: File): Promise<LicenseRequestMeta[]> {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = function (e) {

            const harText = e.target?.result;
            if (typeof harText !== 'string') {
                reject(new Error('Failed to read file!'));
            } else {
                const har = JSON.parse(harText);
                const licenseRequests = har.log.entries
                    .filter((entry: any) => {
                        return entry.request.url.includes('license') && entry.request.method !== 'OPTIONS';
                    })
                    .map((entry: any) => {
                        return {
                            url: `${entry.request.method} ${entry.request.url}`,
                            request: entry.request.postData?.text,
                            response: entry.response.content?.text
                        }
                    });
                    resolve(licenseRequests);
            }

        }
        reader.readAsText(file);
    });
}