import React, { useCallback, useEffect, useRef } from 'react';

export const useFileDrop = (onFilesReceived: (files: FileList) => void) => {
    const frameDragCounter = useRef(0);
    const [isDraggingOverFrame, setIsDraggingOverFrame] = React.useState(false);

    const handleFrameDrag = useCallback((event: DragEvent) => {
        frameDragCounter.current += event.type === 'dragenter' ? 1 : -1;

        if (frameDragCounter.current === 1) {
            setIsDraggingOverFrame(true);
        }

        if (frameDragCounter.current === 0) {
            setIsDraggingOverFrame(false);
        }
    }, []);

    const resetDragging = useCallback(() => {
        frameDragCounter.current = 0;
        setIsDraggingOverFrame(false);
    }, [setIsDraggingOverFrame]);

    useEffect(() => {
        document.addEventListener('dragenter', handleFrameDrag);
        document.addEventListener('dragleave', handleFrameDrag);
    }, [handleFrameDrag]);

    useEffect(() => {
        const handleWindowDragOverOrDrop = (event: DragEvent) => {
            event.preventDefault();
            if (event.type === 'drop') {
                resetDragging();
            }
        };
        window.addEventListener('dragover', handleWindowDragOverOrDrop);
        window.addEventListener('drop', handleWindowDragOverOrDrop);
        return () => {
            window.removeEventListener('dragover', handleWindowDragOverOrDrop);
            window.removeEventListener('drop', handleWindowDragOverOrDrop);
        };
    }, [resetDragging]);

    const onDrop = useCallback(
        (event: React.DragEvent<HTMLDivElement>) => {
            onFilesReceived(event.dataTransfer.files);
        },
        [onFilesReceived]
    );

    return { isDraggingOverFrame, onDrop };
};
