import { useCallback } from 'react';
import './App.css';
import { useFileDrop } from './useFileDrop';
import { DrmCertificate, getLicenseRequestsFromHar, License, LicenseRequest, SignedDrmCertificate, SignedMessage, SignedMessage_MessageType, signedMessage_MessageTypeToJSON } from './utils';
import { Howl } from 'howler';

import mlem from './mlem.mp3';

function bufferFrom(data: any, format: string) {
  function base64ToBinary(base64: string) {
    const binaryString = atob(base64);
    const length = binaryString.length;
    const bytes = new Uint8Array(length);

    for (let i = 0; i < length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }

    return bytes;
  }
  function binaryToUint8Array(binary: string) {
    return new Uint8Array(binary.split('').map((c) => c.charCodeAt(0)));
  }
  return format === 'base64' ? base64ToBinary(data) : binaryToUint8Array(data);
}

function App() {
  const onFilesReceived = useCallback((files: FileList) => {
    if (files == null) {
      return;
    }

    Array.from(files).forEach(async (file) => {
      const licenseRequests = await getLicenseRequestsFromHar(file);

      console.log('License requests in HAR:', { licenseRequests })

      function parseData(data: any, format = 'base64') {
        const buffer = bufferFrom(data, format);
        const signedMessage = SignedMessage.decode(buffer);

        type Response = {
          type: string;
          data?: any;
        }
        const response: Response = {
          type: signedMessage_MessageTypeToJSON(signedMessage.type ?? SignedMessage_MessageType.UNRECOGNIZED)
        }

        if (signedMessage.msg) {
          switch (signedMessage.type) {
            case SignedMessage_MessageType.SERVICE_CERTIFICATE: {
              const certificate = SignedDrmCertificate.decode(signedMessage.msg);
              if (!certificate.drmCertificate) break;
              const drmCertificate = DrmCertificate.decode(certificate.drmCertificate);
              response.data = DrmCertificate.toJSON(drmCertificate);
              break;
            }
            case SignedMessage_MessageType.LICENSE_REQUEST: {
              const licenseRequest = LicenseRequest.decode(signedMessage.msg);
              response.data = LicenseRequest.toJSON(licenseRequest);
              break;
            }
            case SignedMessage_MessageType.LICENSE: {
              const license = License.decode(signedMessage.msg);
              response.data = License.toJSON(license);
              break;
            }
          }
        }

        return response;
      }

      for (const license of licenseRequests) {
        const { url, request, response } = license;
        console.log('________________________________________');
        console.log('Url:', url);
        if (request) {
          try {
            const { type, data } = parseData(request, 'binary');
            console.log(`Request (${type}):`, data);
          } catch (e) {
            console.error(' -- Failed to parse!', e);
          }
        }
        if (response) {
          try {
            const { type, data } = parseData(response);
            console.log(`Response (${type}):`, data);
          } catch (e) {
            console.error(' -- Failed to parse!', e);
          }
        }

      }

      const sound = new Howl({
        src: [mlem]
      });
      sound.play();
    });
  }, []);
  const { isDraggingOverFrame, onDrop } = useFileDrop(onFilesReceived);

  return (
    <div className={`file-drop ${isDraggingOverFrame ? 'file-drop-active' : ''}`} onDrop={onDrop}>

    </div>
  );
}

export default App;
